import { db } from '../config/firebase';
import { collection, addDoc, getDocs, deleteDoc, doc, query, where } from 'firebase/firestore';
import { Namespace, NamespaceAccount, NamespaceMethod } from '../types/Namespace';

export const namespaceService = {
  // Namespace operations
  async createNamespace(namespace: Omit<Namespace, 'namespace-id'>) {
    const docRef = await addDoc(collection(db, 'namespace'), {
      ...namespace,
      'namespace-id': crypto.randomUUID()
    });
    return docRef;
  },

  async getNamespaces() {
    const querySnapshot = await getDocs(collection(db, 'namespace'));
    return querySnapshot.docs.map(doc => doc.data() as Namespace);
  },

  // Namespace Account operations
  async createNamespaceAccount(account: Omit<NamespaceAccount, 'namespace-account-id'>) {
    const docRef = await addDoc(collection(db, 'namespace-account'), {
      ...account,
      'namespace-account-id': crypto.randomUUID()
    });
    return docRef;
  },

  async getNamespaceAccounts(namespaceId: string) {
    const q = query(
      collection(db, 'namespace-account'),
      where('namespace-id', '==', namespaceId)
    );
    const querySnapshot = await getDocs(q);
    return querySnapshot.docs.map(doc => doc.data() as NamespaceAccount);
  },

  // Namespace Method operations
  async createNamespaceMethod(method: Omit<NamespaceMethod, 'namespace-account-method-id'>) {
    const docRef = await addDoc(collection(db, 'namespace-account-method'), {
      ...method,
      'namespace-account-method-id': crypto.randomUUID()
    });
    return docRef;
  },

  async getNamespaceMethods(namespaceId: string) {
    const q = query(
      collection(db, 'namespace-account-method'),
      where('namespace-id', '==', namespaceId)
    );
    const querySnapshot = await getDocs(q);
    return querySnapshot.docs.map(doc => doc.data() as NamespaceMethod);
  },

  // Add these delete methods
  async deleteNamespace(id: string) {
    try {
      await deleteDoc(doc(db, 'namespace', id));
    } catch (error) {
      console.error('Error deleting namespace:', error);
      throw error;
    }
  },

  async deleteNamespaceAccount(id: string) {
    try {
      await deleteDoc(doc(db, 'namespace-account', id));
    } catch (error) {
      console.error('Error deleting account:', error);
      throw error;
    }
  },

  async deleteNamespaceMethod(id: string) {
    try {
      await deleteDoc(doc(db, 'namespace-account-method', id));
    } catch (error) {
      console.error('Error deleting method:', error);
      throw error;
    }
  }
}; 